._404 {
  text-align: center;
}

._404__link {
  color: #009ddc;
}

@-webkit-keyframes blink {
  0% { color: #61bb46; }
  20% { color: #fdb827; }
  40% { color: #f5821f; }
  60% { color: #e03a3e; }
  80% { color: #963d97; }
  100% { color: #009ddc; }
}

@keyframes blink {
  0% { color: #61bb46; }
  20% { color: #fdb827; }
  40% { color: #f5821f; }
  60% { color: #e03a3e; }
  80% { color: #963d97; }
  100% { color: #009ddc; }
}

.NotFound {
  font-size: 128px;
  line-height: 1;
  text-shadow:
    3px 3px 0 rgb(255, 255, 0),
    6px 6px 0 rgb(255, 0, 255),
    9px 9px 0 rgb(0, 255, 255);

  -webkit-animation: blink .5s alternate infinite ease-in;

          animation: blink .5s alternate infinite ease-in;
}



