.transition-wrapper {
  position: relative;
  -ms-flex-positive: 2;
      flex-grow: 2;
}

.fade-enter,
.fade-exit {
  position: absolute;
  top: 0;
  width: 100%;
  -webkit-transition: opacity .5s cubic-bezier(.08,.88,.43,.98);
  -o-transition: opacity .5s cubic-bezier(.08,.88,.43,.98);
  transition: opacity .5s cubic-bezier(.08,.88,.43,.98);
}

.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
}



