.Navbar {
  font-family: 'Work Sans', sans-serif;
  margin-top: 32px;
  margin-bottom: 32px;
}

.Navbar__wrapper {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

@media screen and (max-width: 900px) {
  .Navbar {
    max-width: 600px;
  }
}

.Navbar__Link {
  margin: 0 10px;
  color: #61bb46;
}

.Navbar__Link--active {
  pointer-events: none;
  text-decoration: underline !important;
}

.Navbar__Link:nth-child(2) {
  color: #fdb827;
}

.Navbar__Link:nth-child(3) {
  color: #f5821f;
}

.Navbar__Link:nth-child(4) {
  color: #e03a3e;
}

.Navbar__Link:nth-child(5) {
  color: #963d97;
}

.Navbar__Link:nth-child(6) {
  color: #009ddc;
}



