.Card {
  line-height: 30px;
  color: #61bb46;
  text-decoration: none;
}

.Card.underline {
  text-decoration: underline;
}

p:nth-child(2n) .Card:not(.definedColor) {
  color: #fdb827;
}

p:nth-child(3n) .Card:not(.definedColor) {
  color: #f5821f;
}

p:nth-child(4n) .Card:not(.definedColor) {
  color: #e03a3e;
}

p:nth-child(5n) .Card:not(.definedColor) {
  color: #963d97;
}

p:nth-child(6n) .Card:not(.definedColor) {
  color: #009ddc;
}

.Card:not(.definedColor):nth-child(3n) {
  color: #fdb827 !important;
}

.Card:not(.definedColor):nth-child(5n) {
  color: #f5821f !important;
}

.Card:not(.definedColor):nth-child(7n) {
  color: #e03a3e !important;
}

.Card:not(.definedColor):nth-child(2n) {
  color: #963d97 !important;
}

.Card:not(.definedColor):nth-child(4n) {
  color: #009ddc !important;
}

.Card > img {
  margin-right: .3rem;
  vertical-align: text-bottom;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}



